/* üìå Ï†ÑÏ≤¥ ÌéòÏù¥ÏßÄ Ïä§ÌÉÄÏùº */
.home-container {
  width: 100%;
  max-width: 1920px;
  min-height: 100vh;
  margin: 0 auto;
  text-align: center;
  font-family: "Arial", sans-serif;
  color: #ffffff;
  background-color: #1a1a1a;
  padding: 50px 0;
}

/* üìå Hero Section */
.hero {
  width: 100%;
  height: 40vh;
  min-height: 432px;
  background: linear-gradient(135deg, #4a90e2, #1c1c1c);
  color: white;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.hero h1 {
  font-size: 3rem;
  margin-bottom: 10px;
}

.hero p {
  font-size: 1.2rem;
  opacity: 0.9;
  margin-bottom: 20px;
}

/* üìå CTA Î≤ÑÌäº */
.cta-button {
  display: inline-block;
  padding: 15px 30px;
  font-size: 1.2rem;
  color: white;
  background: #ff6600;
  border-radius: 5px;
  text-decoration: none;
  transition: background 0.3s ease;
}

.cta-button:hover {
  background: #cc5200;
}

/* üìå Features Section */
.features {
  display: flex;
  flex-wrap: wrap; /* üî• Î∞òÏùëÌòï Ï†ÅÏö© */
  justify-content: center;
  align-items: center;
  gap: 20px;
  width: 100%;
  height: auto; /* üî• ÎÜíÏù¥ ÏûêÎèô Ï°∞Ï†ï */
  padding: 20px;
}

.feature-item {
  width: 30%;
  background: #292929;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 3px 3px 10px rgba(0, 0, 0, 0.3);
  transition: transform 0.3s ease, background 0.3s ease;
}

.feature-item:hover {
  transform: translateY(-5px);
  background: #3a3a3a;
}

.feature-item h2 {
  font-size: 1.5rem;
  color: #ffcc00;
}

.feature-item p {
  font-size: 1rem;
  opacity: 0.8;
}

/* üìå Resume Section */
.resume-section {
  text-align: center;
  margin-top: 50px;
}

.resume-button {
  display: inline-block;
  padding: 15px 30px;
  font-size: 1.2rem;
  color: white;
  background: #ff6600;
  border-radius: 5px;
  text-decoration: none;
  transition: background 0.3s ease, transform 0.2s ease;
  box-shadow: 3px 3px 10px rgba(0, 0, 0, 0.2);
}

.resume-button:hover {
  background: #cc5200;
  transform: scale(1.05);
}

/* üìå üî•üî• Î∞òÏùëÌòï Ï†ÅÏö© üî•üî• */
@media screen and (max-width: 768px) {
  .hero h1 {
      font-size: 2rem; /* ÏûëÏùÄ ÌôîÎ©¥ÏóêÏÑú Í∏ÄÏî® ÌÅ¨Í∏∞ Ï°∞Ï†ï */
  }

  .hero p {
      font-size: 1rem;
  }

  .cta-button {
      font-size: 1rem;
      padding: 12px 24px;
  }

  .features {
      flex-direction: column; /* Î™®Î∞îÏùºÏóêÏÑúÎäî ÏÑ∏Î°ú Ï†ïÎ†¨ */
      gap: 10px;
      padding: 10px;
  }

  .feature-item {
      width: 90%; /* Î™®Î∞îÏùºÏóêÏÑúÎäî Ìïú Ï§ÑÎ°ú */
      font-size: 1rem;
  }

  .resume-section {
      margin-top: 30px;
  }

  .resume-button {
      font-size: 1rem;
      padding: 12px 24px;
  }
}
