.projects-container {
    text-align: center;
    max-width: 1920px;
    margin: auto;
    padding: 40px 20px;
    color: #fff;
    background-color: #1a1a1a;
  }
  
  /* ÌîÑÎ°úÏ†ùÌä∏ Í∞úÎ≥Ñ ÏÑπÏÖò Ïä§ÌÉÄÏùº */
  .project {
    
    background: #292929;
    max-width: 800px;
    padding: 20px;
    margin: auto;
    border-radius: 10px;
    box-shadow: 3px 3px 10px rgba(0, 0, 0, 0.3);
    margin-bottom: 40px;
  }
  
  .project h2 {
    font-size: 2rem;
