/* Ï†ÑÏ≤¥ ÌéòÏù¥ÏßÄÎ•º Ï§ëÏïô Ï†ïÎ†¨ */
.contact-page {
    display: flex;
    justify-content: center; /* Í∞ÄÎ°ú Ï§ëÏïô Ï†ïÎ†¨ */
    align-items: center; /* ÏÑ∏Î°ú Ï§ëÏïô Ï†ïÎ†¨ */
    height: 100vh; /* ÌôîÎ©¥ Ï†ÑÏ≤¥ ÎÜíÏù¥ Ï∞®ÏßÄ */
    background-color: #1a1a1a; /* Ï†ÑÏ≤¥ Î∞∞Í≤ΩÏÉâ */
  }
  
  /* Ïó∞ÎùΩÏ≤ò Î∞ïÏä§ Ïä§ÌÉÄÏùº */
  .contact-container {
    text-align: center;
    max-width: 600px;
    padding: 40px 20px;
    background-color: #292929; /* üî• ÌöåÏÉâ Î∞∞Í≤Ω */
    color: #fff;
    border-radius: 10px;
    box-shadow: 3px 3px 10px rgba(0, 0, 0, 0.3); /* üî• Í∑∏Î¶ºÏûê Ìö®Í≥º */
  }

  .contact-container h1 {
    font-size: 2rem;
    margin-bottom: 20px;
    color: #4a90e2;
  }
  
  .contact-item {
    font-size: 1.2rem;
    margin: 15px 0;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 10px;
  }
  
  .contact-item a {
    color: #ffcc00;
    text-decoration: none;
    font-weight: bold;
  }
  
  .contact-item a:hover {
    text-decoration: underline;
  }
  
  .contact-item svg {
    font-size: 1.5rem;
    color: #4a90e2;
  }
  