/* üîπ ÎÑ§ÎπÑÍ≤åÏù¥ÏÖò Î∞î Ïä§ÌÉÄÏùº */
.navbar {
  width: 100%;
  height: 60px;
  background-color: #333;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 20px;
  position: relative;
}

/* üîπ Î°úÍ≥† Ïä§ÌÉÄÏùº */
.logo {
  color: white;
  font-size: 24px;
  font-weight: bold;
  text-decoration: none;
}

/* üîπ ÌñÑÎ≤ÑÍ±∞ Î≤ÑÌäº */
.menu-icon {
  font-size: 28px;
  color: white;
  cursor: pointer;
  display: block;
  position: absolute;
  right: 20px;
  top: 15px;
  z-index: 1000;
}

/* üîπ ÎÑ§ÎπÑÍ≤åÏù¥ÏÖò Î©îÎâ¥ (Í∏∞Î≥∏Ï†ÅÏúºÎ°ú Ïà®ÍπÄ) */
.nav-menu {
  position: absolute;
  top: 60px;
  right: 0px;
  width: 200px;
  background-color: rgba(51, 51, 51, 0.95);
  border-radius: 10px;
  display: none;
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.3s ease-in-out, visibility 0.3s ease-in-out;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.3);
  padding: 10px 0;
  text-align: right;
  flex-direction: column;
}

/* üîπ ÌñÑÎ≤ÑÍ±∞ Î≤ÑÌäº ÌÅ¥Î¶≠ Ïãú Î©îÎâ¥ Î≥¥Ïù¥Í∏∞ */
.nav-menu.active {
  right: 0;
  display: flex;
  opacity: 1;
  visibility: visible;
}

/* üîπ ÎÑ§ÎπÑÍ≤åÏù¥ÏÖò Î≤ÑÌäº Ïä§ÌÉÄÏùº */
.nav-menu a {
  display: block;
  color: white;
  text-decoration: none;
  font-size: 18px;
  padding: 12px 20px;
  transition: background 0.3s ease-in-out;
  border-radius: 5px;
}

/* üîπ ÎÑ§ÎπÑÍ≤åÏù¥ÏÖò Î≤ÑÌäº hover Ìö®Í≥º */
.nav-menu a:hover {
  background-color: rgba(255, 255, 255, 0.2);
}

/* üîπ Î™®Î∞îÏùº & PC Í≥µÌÜµ: Ï≤òÏùåÏóêÎäî Î©îÎâ¥ Ïà®Í∏∞Í∏∞ */
@media screen and (max-width: 1024px) {
  .nav-menu {
    display: none;
  }

  .menu-icon {
    display: block;
  }
}
