/* üîπ Í∏∞Î≥∏ Ïä§ÌÉÄÏùº */
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: "Noto Sans KR", sans-serif;
  }
  
  body {
    width: 100%;
    max-width: 1920px;  /* ÏµúÎåÄ ÎÑàÎπÑ Ï†úÌïú */
    min-height: 100vh;  /* ÏµúÏÜå ÎÜíÏù¥ 100% */
    background-color: #f8f9fa; /* Í∏∞Î≥∏ Î∞∞Í≤ΩÏÉâ */
    color: #333;
    overflow-x: hidden; /* Í∞ÄÎ°ú Ïä§ÌÅ¨Î°§ Ï†úÍ±∞ */
  }
  
  /* üîπ Í≥µÌÜµ Î†àÏù¥ÏïÑÏõÉ Ïä§ÌÉÄÏùº */
  .container {
    width: 90%;
    max-width: 1200px;
    margin: 0 auto;
