      height: auto;
      padding: 10px 0;
    }
  
    .nav-menu {
      display: flex;
      flex-direction: column;
      align-items: center;
    }
  
    .stack-list {
      display: grid;
      grid-template-columns: repeat(2, 1fr); /* 2Ïó¥ Î∞∞Ïπò */
      gap: 10px;
    }
  
    .project-slider img {
      width: 100%; /* Ïù¥ÎØ∏ÏßÄÍ∞Ä Î™®Î∞îÏùº ÌôîÎ©¥Ïóê ÎßûÍ≤å Ï°∞Ï†ï */
      max-width: 400px;
    }
  
    .contact-container {
