/* Ï†ÑÏ≤¥ ÌéòÏù¥ÏßÄ Ï§ëÏïô Ï†ïÎ†¨ */
.stack-page {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
 
  background-color: #1a1a1a;
  padding: 20px; /* Î™®Î∞îÏùºÏóêÏÑú ÏûòÎ¶¨Í±∞ÎÇò ÍΩâ Ï∞®Îäî ÌòÑÏÉÅ Î∞©ÏßÄ */
}

/* Í∏∞Ïà† Ïä§ÌÉù Ïª®ÌÖåÏù¥ÎÑà */
.stack-container {
  text-align: center;
  max-width: 700px;
  min-height: 30vh;
  padding: 40px 20px;
  background-color: #292929; /* üî• Ïñ¥ÎëêÏö¥ Î∞∞Í≤Ω */
  color: #fff;
  border-radius: 10px;
  box-shadow: 3px 3px 10px rgba(0, 0, 0, 0.3);
}

/* Ï†úÎ™© Ïä§ÌÉÄÏùº */
.stack-container h1 {
  font-size: 2.5rem;
  margin-bottom: 20px;
  color: #4a90e2;
}

/* ÏÑ§Î™Ö Ïä§ÌÉÄÏùº */
.stack-description {
  font-size: 1.2rem;
  margin-bottom: 20px;
  color: #ccc;
}

/* Í∏∞Ïà† Ïä§ÌÉù Î¶¨Ïä§Ìä∏ */
.stack-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
}

/* Í∞úÎ≥Ñ Í∏∞Ïà† ÏïÑÏù¥ÌÖú */
.stack-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 120px;
  height: 120px;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 10px;
  box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.3);
  font-size: 1rem;
  font-weight: bold;
  color: #fff;
  transition: transform 0.3s ease-in-out, background 0.3s;
}

/* ÏïÑÏù¥ÏΩò Ïù¥ÎØ∏ÏßÄ */
.icon-img {
  width: 50px;
  height: 50px;
  margin-bottom: 10px;
}

/* Ìò∏Î≤Ñ Ìö®Í≥º */
.stack-item:hover {
  transform: scale(1.1);
  background: rgba(255, 255, 255, 0.2);
}

/* üîπ Î∞òÏùëÌòï: Î™®Î∞îÏùºÏóêÏÑúÎäî Îëê Ï§Ñ Ï†ïÎ†¨ */
@media screen and (max-width: 768px) {
  .stack-list {
    display: grid;
    grid-template-columns: repeat(2, 1fr); /* 2Ïó¥(Îëê Ï§Ñ)Î°ú ÌëúÏãú */
    gap: 15px;
  }

  .stack-item {
    width: 140px;
    height: 140px;
    font-size: 1.1rem;
  }

  .icon-img {
    width: 60px;
    height: 60px;
  }
}

/* üîπ ÏûëÏùÄ Î™®Î∞îÏùº (480px Ïù¥Ìïò)ÏóêÏÑúÎäî Ìïú Ï§ÑÏóê 1Í∞ú */
@media screen and (max-width: 480px) {
  .stack-list {
    grid-template-columns: repeat(1, 1fr); /* Ìïú Ï§ÑÏóê 1Í∞ú */
  }
}
